/* Basic styling */
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* Header styling */
.header {
  background-color: #f0f0f0;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header h1 {
  font-size: 2em;
  margin: 0;
}

/* Navigation bar styling */
.nav {
  background-color: #e0e0e0;
  padding: 10px;
}

.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav li {
  margin-right: 20px;
}

.nav a {
  text-decoration: none;
  color: #333;
}

/* Main content styling */
.main-content {
  padding: 20px;
}

.main-content h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
}

/* Form styling */
.form {
  padding: 20px;
  border: 1px solid #ddd;
}

.form label {
  display: block;
  margin-bottom: 5px;
}

.form input[type="text"],
.form input[type="date"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
}

.form button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}

/* Remaining styles from the second CSS */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Transaction table styling */
.transaction-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.transaction-table th,
.transaction-table td {
  padding: 10px;
  border: 1px solid #ddd;
}

.transaction-table th {
  font-weight: bold;
  text-align: left;
}

.transaction-table td {
  text-align: left;
}

.sort-select {
  margin-bottom: 10px;
}